.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  padding-top: 0;
  overflow: auto;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease; }
  .modal.active {
    background-color: rgba(201, 201, 201, 0.5);
    opacity: 1;
    visibility: visible; }
  .modal__content {
    position: relative;
    padding: 20px 0;
    margin: 10px;
    background-color: white;
    border: 1px solid #eee;
    border-radius: 10px; }
    @media (min-width: 576px) {
      .modal__content {
        margin: 50px; } }
  .modal__text-area {
    width: 50%;
    height: 100px; }
  .modal__btn {
    margin: 10px; }
  .modal__close {
    margin-left: 10px; }
  .modal__title {
    text-align: center; }
  .modal__sub-title {
    margin-left: 10px; }



