.btn {
  background-color: #eee;
  border: none;
  border-radius: 5px;
  color: black;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  -webkit-transition-duration: 0.4s;
  -o-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer; }
  .btn:hover {
    background-color: white;
    -webkit-box-shadow: 0px 22px 26px -9px rgba(0, 0, 0, 0.29);
    box-shadow: 0px 22px 26px -9px rgba(0, 0, 0, 0.29); }
  .btn--wide {
    background-color: #48ad4e;
    color: white;
    width: 100%; }
    .btn--wide:hover {
      background-color: #1b6d22; }
  .btn--close {
    background-color: #b45656;
    color: white; }
    .btn--close:hover {
      background-color: #8a2424; }
  .btn--add {
    background-color: #38b45d;
    color: white; }
    .btn--add:hover {
      background-color: #276e19; }
  .btn--black {
    background-color: #3a3a3a;
    color: white; }
    .btn--black:hover {
      background-color: black; }
  .btn--red {
    background-color: #a84545;
    color: white; }
    .btn--red:hover {
      background-color: #6e2121; }
  .btn--green {
    background-color: #1b9621;
    color: white; }
    .btn--green:hover {
      background-color: #135e19; }
  .btn--blue {
    background-color: #4655a5;
    color: white; }
    .btn--blue:hover {
      background-color: #1d2570; }



