.drop-zone {
  border: 1px dotted #5f5f5f;
  display: block;
  width: 80%;
  height: 200px;
  margin: 10px; }
  @media (min-width: 576px) {
    .drop-zone {
      width: 300px; } }
  .drop-zone:hover {
    background-color: #9ae09a6c;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s; }
  .drop-zone__field {
    display: block;
    position: relative;
    width: 100%;
    height: 100%; }
  .drop-zone__placeholder, .drop-zone__sucess {
    padding: 10px;
    text-align: center;
    color: #929292;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%); }
  .drop-zone__sucess {
    color: #509266;
    width: 100%; }



