.text-area {
  margin: 10px 0 10px 10px;
  position: relative; }
  .text-area__field {
    height: 100px;
    padding: 10px;
    border-radius: 5px;
    width: 80%;
    border: 1px solid #eee; }
    @media (min-width: 576px) {
      .text-area__field {
        width: 300px; } }
  .text-area__error {
    margin: 0;
    color: #be2d2d; }



